a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1, h2, h3, h4, p, ul {
  margin: 0;
  padding: 0;
}

button {
  border: none;
}

img {
  width: 100%;
  max-width: 100%;
  display: block;
}

body {
  color: #f0f0f0;
  background-color: #12121a;
  margin: 0;
  font-family: Fira Code, Segoe UI, sans-serif;
  line-height: 1.6;
  transition: all .3s;
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

@media screen and (width >= 768px) {
  .container {
    width: 768px;
    max-width: none;
  }
}

@media screen and (width >= 1200px) {
  .container {
    width: 1200px;
    max-width: none;
  }
}

header {
  z-index: 10;
  background-color: #1e1e2f;
  padding: 1.2rem 0;
  position: sticky;
  top: 0;
  box-shadow: 0 4px 10px #0000004d;
}

header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

header h1 {
  letter-spacing: 1px;
  color: #00bcd4;
  margin: 0;
  font-size: 1.8rem;
}

header .signup-btn {
  color: #12121a;
  cursor: pointer;
  background-color: #00bcd4;
  border: 2px solid #0000;
  border-radius: 6px;
  padding: .5rem 1.2rem;
  font-weight: 600;
  transition: all .3s;
  box-shadow: 0 2px 6px #00bcd466;
}

header .signup-btn:hover {
  color: #00bcd4;
  background-color: #0000;
  border: 2px solid #00bcd4;
}

.blogs {
  margin-top: 2.5rem;
}

.blogs__post {
  background: linear-gradient(145deg, #1f1f30, #2b2b3d);
  border-radius: 10px;
  margin-bottom: 2rem;
  padding: 1.5rem;
  transition: all .3s;
  position: relative;
  box-shadow: 0 6px 18px #00000040;
}

.blogs__post:hover {
  border-left-color: #00acc1;
}

.blogs__actual {
  z-index: 2;
}

.blogs__title {
  color: #00bcd4;
  margin-top: 0;
  line-height: 32px;
}

.blogs__author {
  color: #f0f0f0;
  margin-top: 0;
  font-size: 18px;
  line-height: 24px;
}

.blogs__text {
  color: #f0f0f0;
}

.blogs__actions {
  gap: 10px;
  padding-top: 1rem;
  display: flex;
}

.blogs__btn {
  color: #00bcd4;
  cursor: pointer;
  fill: #00bcd4;
  background-color: #0000;
  border: 2px solid #00bcd4;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  height: 35px;
  padding: 0 15px;
  font-weight: bold;
  transition: all .3s;
  display: flex;
}

.blogs__btn:hover {
  color: #12121a;
  background-color: #00bcd4;
  transform: translateY(-1px);
}

.blogs__btn--like, .blogs__btn--dislike {
  color: #00bcd4;
  cursor: pointer;
  fill: #00bcd4;
  background-color: #0000;
  border-color: #00bcd4;
  border-radius: 4px;
  padding: .4rem 1rem;
  font-weight: bold;
  transition: all .3s;
}

.blogs__btn--like:hover, .blogs__btn--dislike:hover {
  color: #12121a;
  background-color: #00bcd4;
  transform: translateY(-1px);
}

.blogs__btn--comment--clicked {
  color: #12121a;
  fill: #12121a;
  background-color: #00bcd4;
}

.blogs__likesvg {
  fill: #00bcd4;
  width: 18px;
  height: 18px;
  transition: all .3s;
}

.blogs__dislikesvg {
  fill: #00bcd4;
  width: 18px;
  height: 18px;
  transition: all .3s;
  transform: rotate(180deg);
}

.blogs__commentsvg {
  width: 18px;
  height: 18px;
  fill: inherit;
  transition: all .3s;
}

.blogs__btn:hover .blogs__likesvg, .blogs__btn:hover .blogs__dislikesvg, .blogs__btn:hover .blogs__commentsvg {
  fill: #12121a;
}

.blogs__comments {
  z-index: -1;
  background: linear-gradient(0deg, #1f1f30, #4b4b71);
  border-bottom: 4px solid #00bcd4;
  border-radius: 0 0 10px 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 1180px;
  height: 143px;
  padding: 18px 10px 10px;
  transition: all .3s;
  display: flex;
  position: absolute;
  top: 180px;
  left: 0;
  overflow: hidden;
  transform: translate(0, -170px);
}

.blogs__comment {
  background-color: #0000004d;
  border-radius: 10px;
  width: calc(100% - 20px);
  padding: 10px;
}

.comments--down {
  transition: all .3s;
  overflow: visible;
}
